import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Select, Input, Button, Radio, Spin, message } from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            roleId: "",
            account: "",
            password: "",
            confirmPassword: "",
            name: "",
            mobile: "",
            status: "0",
            loading: true
        };
        ['handleSubmit', 'checkPassword', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        let that = this;
        if(this.props.type == "add") {
            this.setState({loading: false})
        } else {
            ajax({
                url: "/auth/manage/getUserDetail",
                method: "post",
                data: {
                    id: this.props.userID
                },
                callback: function(data) {
                    that.setState(
                        {
                            loading: false,
                            roleId: data.data.roleId,
                            account: data.data.account,
                            password: data.data.password,
                            confirmPassword: data.data.confirmPassword,
                            name: data.data.name,
                            mobile: data.data.mobile,
                            status: data.data.status
                        }
                    )
                }
            });
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type == "edit") {
                    values = Object.assign(values, {id: this.props.userID})
                }
                ajax({
                    url: "/auth/manage/updateUser",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("添加管理员成功!", 1, function() {
                                hashHistory.push("/auth/manage")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    checkPassword(rule, value, callback) {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('两次密码输入不一致!');
        } else {
            callback();
        }
    }

    returnList() {
        hashHistory.push("/auth/manage");
    }

    resetForm() {
        this.props.form.resetFields();
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 6 }
        };

        return (
            <Spin spinning={this.state.loading} tip="Loading...">
            <Form>
                <FormItem
                    {...formItemLayout}
                    label="账号类型"
                    hasFeedback
                >
                    {getFieldDecorator('roleId', {
                        rules: [
                            { required: true, message: '请选择管理员类型!' }
                        ],
                        initialValue: this.state.roleId.toString()
                    })(
                        <Select placeholder="请选择账户类型">
                            <Option value="1">超级管理员</Option>
                            <Option value="2">用户管理员</Option>
                            <Option value="3">项目管理员</Option>
                            <Option value="4">资金管理员</Option>
                            <Option value="5">其他管理员</Option>
                        </Select>
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="登录账号"
                    hasFeedback
                >
                    {getFieldDecorator('account', {
                        rules: [{
                            required: true, message: '请输入登录账号!'
                        }],
                        initialValue: this.state.account
                    })(
                        <Input placeholder="请输入登录账号" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="登录密码"
                    hasFeedback
                >
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '请输入登录密码!'
                        }],
                        initialValue: this.state.password
                    })(
                        <Input type="password" placeholder="请输入登录密码" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="确认密码"
                    hasFeedback
                >
                    {getFieldDecorator('confirmPassword', {
                        rules: [{
                            required: true, message: '请再次输入密码'
                        },{
                            validator: this.checkPassword
                        }],
                        initialValue: this.state.password
                    })(
                        <Input type="password" placeholder="请确认新密码" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="管理员姓名"
                    hasFeedback
                >
                    {getFieldDecorator('name', {
                        rules: [{
                            required: true, message: '请输入管理员姓名!'
                        }],
                        initialValue: this.state.name
                    })(
                        <Input placeholder="请输入管理员姓名" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="联系电话"
                    hasFeedback
                >
                    {getFieldDecorator('mobile', {
                        rules: [{
                            required: true, message: '请输入联系电话!'
                        }],
                        initialValue: this.state.mobile
                    })(
                        <Input placeholder="请输入联系电话" />
                    )}
                </FormItem>
                <FormItem
                    {...formItemLayout}
                    label="状态"
                >
                    {getFieldDecorator('status', {
                        initialValue: this.state.status.toString()
                    })(
                        <RadioGroup>
                            <Radio value="0">禁用</Radio>
                            <Radio value="1">启用</Radio>
                        </RadioGroup>
                    )}
                </FormItem>
                <FormItem
                    wrapperCol={{ span: 12, offset: 6 }}
                >
                    <Button type="primary" htmlType="submit" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button>
                    <Button onClick={this.returnList} style={{"marginRight": "20px"}}>取消</Button>
                    <Button type="danger" onClick={this.resetForm}>重置</Button>
                </FormItem>
            </Form>
                </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


